import React, { useState } from "react";
import NavBar from "../../components/NavBar/NavBar";
import Footer from "../../components/Footer/Footer";
import "./Game.scss";

const GameSubpage = () => {
  const [isMobileProductListOpen, setIsMobileProductListOpen] = useState(false);

  const handleCloseNavbar = () => {
    setIsMobileProductListOpen(false);
  };

  const iframeRef = React.createRef();

  return (
    <>
      <NavBar
        isMobileProductListOpen={isMobileProductListOpen}
        onClose={handleCloseNavbar}
      />
      <div className="gameContainer">
        <iframe
          ref={iframeRef}
          src={`${process.env.PUBLIC_URL}/game/index.html`}
          frameBorder="0"
          title="game"
          allowFullScreen
          className="mobileGame"
        ></iframe>
      </div>
      <Footer />
    </>
  );
};

export default GameSubpage;
